import React, { PureComponent, Suspense } from "react";
import { Swiper, SwiperSlide } from 'swiper/react'
import { GET_MEDIA_NAME } from "../../../../utils";
import 'swiper/css';
const Image = React.lazy(() => import('../../../Themes/Elements/Media/Image'));
const ReelVideo = React.lazy(() => import('./ReelVideo'));


const mediaDimension = JSON.parse(window.localStorage.getItem('mediaDimension'));
export default class ReelMediaSlider extends PureComponent {
       constructor(props) {
              super(props);
              this.state = {
                     mediaSize: {
                            height: mediaDimension != null ? mediaDimension[0] : '85vh',
                            width: mediaDimension != null ? mediaDimension[1] : '85vh',
                     }
              };
       }
       mediaSize = mediaSize => {
              this.props.mediaSizeCallback(mediaSize);
       }
       CurrentVideo = e => {
              //console.log("ðŸš€ ~ ReelMedia ~ e:", e)

       }
       render() {
              const { imageList, data, wall, wallID, ownerId, size } = this.props
              const muted = false
              const autoPlay = true
              const pagination = {
                     clickable: true,
                     bulletClass: `tb_rp_bullet`,
              }
              return <div className="tb_rp_media_holder">
                     <Swiper
                            ref={this.splideRef}
                            direction="horizontal"
                            passiveListeners={true}
                            pagination={pagination}
                     >
                            {imageList.map((imageItem, index) => <SwiperSlide key={index}>
                                   {({ isActive }) => <>{(imageItem.type === 2 || imageItem.type === 4 || !isActive) ?
                                          <img className="tb_rp_s_image" src={imageItem.postFile} height={500} width={480} alt={GET_MEDIA_NAME(imageItem.postFile)} />
                                          : (imageItem.type === 3 || imageItem.type === 5) ?
                                                 <Suspense fallback={<></>}><ReelVideo VideoClass="tb_rp_video" data={{ ...imageItem, network: { id: imageItem.networkId }, postFileNew: imageItem.postFile, mediaUrl: imageItem.mediaFile }} wallID={wallID} themeID={wall.Personalization.widgetTheme} ownerId={ownerId} size={size} isCover={true} controls={false} autoPlay={isActive} muted={muted} handleVideoEnded={(e) => this.CurrentVideo()} index={index} /></Suspense> : null}</>}
                            </SwiperSlide>
                            )}
                     </Swiper>

              </div>
       }
}